import React, { ReactNode } from 'react';
import { useDefaultProp } from '../../common/react/useDefaultProp';
import { RadioWrap, RadioInput, RadioIcon, RadioText } from './styled';

type IRadioProps = {
  /**
   * 默认是否选中
   */
  defaultChecked?: boolean;
  /**
   * 是否选中
   */
  checked?: boolean;
  /**
   * 变化事件
   */
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
  /**
   * 禁用
   */
  disabled?: boolean;
  /**
   * 子元素
   */
  children?: ReactNode;
};

export const Radio = React.memo(
  React.forwardRef<HTMLInputElement, IRadioProps>(
    (props, ref) => {

      const {
        defaultChecked = false,
        checked: _checked,
        onChange,
        disabled = false,
        children,
      } = props;

      const [checked, setChecked] = useDefaultProp(
        defaultChecked,
        _checked,
        onChange,
        (e: React.ChangeEvent<HTMLInputElement>) => e.target.checked
      );

      return <RadioWrap disabled={!!disabled}>
        <RadioInput
          ref={ref}
          type="radio"
          onChange={setChecked}
          disabled={disabled}
          checked={checked}
        />
        <RadioIcon disabled={!!disabled} checked={!!checked} />
        {children && <RadioText>{children}</RadioText>}
      </RadioWrap>;
    }
  )
);
